                        "brief":"操作元素的 class 列表和内联样式是数据绑定的一个常见需求",
                        "active":"https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=835093052,2901387644&fm=26&gp=0.jpg",
                        "text":"操作元素的 class 列表和内联样式是数据绑定的一个常见需求。因为它们都是 attribute，所以我们可以用 v-bind 处理它们：只需要通过表达式计算出字符串结果即可。不过，字符串拼接麻烦且易错。因此，在将 v-bind 用于 class 和 style 时，Vue.js 做了专门的增强。表达式结果的类型除了字符串之外，还可以是对象或数组。我们可以传给 v-bind:class 一个对象，以动态地切换 class：上面的语法表示 active 这个 class 存在与否将取决于数据 property isActive 的 truthiness。你可以在对象中传入更多字段来动态切换多个 class。此外，v-bind:class 指令也可以与普通的 class attribute 共存。当有如下模板：当 isActive 或者 hasError 变化时，class 列表将相应地更新。例如，如果 hasError 的值为 true，class 列表将变为static active text-danger绑定的数据对象不必内联定义在模板里：渲染的结果和上面一样。我们也可以在这里绑定一个返回对象的计算属性。这是一个常用且强大的模式：我们可以把一个数组传给 v-bind:class，以应用一个 class 列表：如果你也想根据条件切换列表中的 class，可以用三元表达式：这样写将始终添加 errorClass，但是只有在 isActive 是 truthy[1] 时才添加 activeClass。不过，当有多个条件 class 时这样写有些繁琐。所以在数组语法中也可以使用对象语法：当在一个自定义组件上使用 class property 时，这些 class 将被添加到该组件的根元素上面。这个元素上已经存在的 class 不会被覆盖。",
