                        "text":"把数组转换为字符串JavaScript 方法 toString() 把数组转换为数组值（逗号分隔）的字符串。join() 方法也可将所有数组元素结合为一个字符串。它的行为类似 toString()，但是您还可以规定分隔符：Popping 和 Pushing在处理数组时，删除元素和添加新元素是很简单的。Popping 和 Pushing 指的是：从数组弹出项目，或向数组推入项目。Poppingpop() 方法从数组中删除最后一个元素pop() 方法返回“被弹出”的值：Pushingpush() 方法（在数组结尾处）向数组添加一个新的元素：push() 方法返回新数组的长度：位移元素位移与弹出等同，但处理首个元素而不是最后一个。shift() 方法会删除首个数组元素，并把所有其他元素“位移”到更低的索引。shift() 方法返回被“位移出”的字符串：unshift() 方法（在开头）向数组添加新元素，并“反向位移”旧元素：unshift() 方法返回新数组的长度。更改元素通过使用它们的索引号来访问数组元素：数组索引（下标）以 0 开始。[0] 是第一个数组元素，[1] 是第二个，[2] 是第三个 ...length 属性提供了向数组追加新元素的简易方法：删除元素既然 JavaScript 数组属于对象，其中的元素就可以使用 JavaScript delete 运算符来删除：使用 delete 会在数组留下未定义的空洞。请使用 pop() 或 shift() 取而代之。",
