        components: {
            "heade": heade,
            "foot": foot,
            "back": back
        },
        mounted() {
            document.getElementById("app").onclick = e => {
                this.createWave(e);
                this.intervalClearWave();
            };
            window.addEventListener('scroll', this.handleScroll) //window添加一个滚动监听事件，
        },
        methods: {
            createWave(e) {
                // 让新生成的波纹始终在之前波纹的上层产生叠加效果
                if (this.wavesConfig.zIndexCount > 99999) {
                    this.wavesConfig.zIndexCount = 999;
                } else {
                    this.wavesConfig.zIndexCount++;
                }
                // 在一定范围内随机生成波纹的大小
                const waveSize = parseInt(
                    Math.random() * (this.wavesConfig.maxSize - this.wavesConfig.minSize) +
                    this.wavesConfig.minSize
                );
                //添加新的波纹数据
                this.waves.push({
                    left: `${e.clientX - waveSize / 2}px`,
                    top: `${e.clientY - waveSize / 2}px`,
                    zIndex: this.wavesConfig.zIndexCount,
                    width: `${waveSize}px`,
                    height: `${waveSize}px`
                });
            },
            intervalClearWave() {
                clearTimeout(this.clear.timeoutId);
                this.clear.timeoutId = setTimeout(() => {
                    this.waves = [];
                }, this.clear.delay);
            },

            handleScroll() {
                // 页面滚动距顶部距离
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop ||
                    document.body.scrollTop;

                let scroll = scrollTop - this.i;//当前的页面高度
                this.i = scrollTop;//
                if (scroll < 0) {
                    //往下
                    this.searchFixed=true;
                } else {
                    //往上
                    this.searchFixed=false;
                }
            },
            destroyed() {
                window.removeEventListener('scroll', this.handleScroll) //销毁滚动监听事件
            },
        }
    }
</script>

