<template>
    <div id="messages">
        <div class="content animate__animated animate__zoomIn">
            <div class="messages-head " ><!--留言板头部部分-->
                <div class="head-img"><!--头部图片-->
                    <img :src="messages.active" alt="">
                </div>
                <div class="title"><!--图片旁边的文字-->
                    留言板
                    <div class="text">
                        <i class="iconfont icon-guanyuwomen"></i><span>{{messages.name}}</span>
                        <i class="iconfont icon-shijian-copy"></i><span>{{messages.time}}</span>
                        <i class="iconfont icon-yanjing"></i><span>{{messages.number}}</span>
                        <i class="iconfont icon-dianzan"></i><span>{{numberLikes}}</span>
                        <transition name="black">
                            <span class="one" v-show="count>0 && count<2">+1</span>
                        </transition>
                    </div>
                </div>
            </div>
            <div class="head-content"><!--头部图片下方文字-->
                <div class="text">{{messages.welcome}}</div>
                <div class="text">{{messages.friendship}}</div>
                <div class="text">{{messages.not}}</div>
            </div>
            <div class="fabulous-share"><!--点赞和分享的按钮-->
                <div class="fabulous" @click="open" ><i class="iconfont icon-dianzan"></i><span>点赞</span></div>
                <div class="share" @click="share = true" ><i class="iconfont icon-friend-link"></i><span>分享</span></div>
                <div class="big-circular" @click="circular = true"><div class="small-circular"><i class="iconfont icon-qian"></i></div></div>
                <!--分享的对话框-->
                <el-dialog
                        :visible.sync="share"
                        width="20%"
                        center
                        append-to-body>
                    <div style="margin-bottom: 20px">选择分享方式:</div>
                    <div style="text-align: center">
                        <i class="iconfont icon-weibo" style="font-size: 30px;color:orange;margin-right: 30px;cursor: pointer" @click="sign"></i>
                        <i class="iconfont icon-weixin1" style="font-size: 30px;color:greenyellow;margin-right: 30px;cursor:pointer" @click="sign"></i>
                        <i class="iconfont icon-qq" style="font-size: 30px;color:#3ea6ff;cursor:pointer" @click="sign"></i>
                    </div>
                    <span slot="footer" class="dialog-footer">
                          <el-button @click="share = false">取 消</el-button>
                          <el-button type="primary" @click="share = false">确 定</el-button>
                    </span>
                </el-dialog>
                <!--中间钱的对话框-->
                <el-dialog
                        :visible.sync="circular"
                        width="20%"
                        center
                        append-to-body>
                    <div style="margin-bottom: 20px;">选择扫码方式</div>
                    <div style="width: 100px;height: 100px;border:1px solid black;text-align: center;margin: 0 auto">
                        暂无二维码
                    </div>
                    <div style="margin-top:20px;">多少都是心意，谢谢大家！</div>


                    <span slot="footer" class="dialog-footer">
                          <el-button @click="circular = false">取 消</el-button>
                          <el-button type="primary" @click="circular = false">确 定</el-button>
                    </span>
                </el-dialog>
            </div>


            <div class="messages-list" ><!--评论部分-->
                <div class="bor"><i class="iconfont icon-yezi"></i><h2>评论列表</h2></div>
                <div class="list" v-for="(item,index) in textCopy" :key="index" >
                    <div class="list-content" >
                        <div class="list-number">{{item.number}}楼</div>
                        <div class="list-img">
                            <img :src="item.active" >
                        </div>
                        <div class="list-name">
                            <span>{{item.name}}</span>
                            <span>({{item.time}})</span>
                            <div class="list-text">{{item.content}}</div>
                        </div>

                    </div>
                </div>
                <!--分页器组件-->
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[5, 10, 20, 40]"
                        :page-size="pagesize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="msg_arr.length"
                ></el-pagination>
                <!--page-size每页显示的个数，total总数，pageSize 改变时会触发，currentPage 改变时会触发-->
            </div >
            <div class="publish-messages"><!--input的组件-->
                <div class="bor"><i class="iconfont icon-xie"></i><h2>发表评论</h2></div>
                <el-input
                        style="width: 40%;display: inline-block;margin:20px"
                        placeholder="请输入名字"
                        v-model="inputName"
                        clearable>
                </el-input>
                <el-input
                        style="width: 40%;display: inline-block;margin:20px"
                        placeholder="请输入图片链接"
                        v-model="inputImg"
                        clearable>
                </el-input>
                <el-input
                        style="margin:20px;width: 90%"
                        type="textarea"
                        :rows="3"
                        placeholder="请输入内容"
                        v-model="textarea">
                </el-input>

                <el-button type="primary" round style="margin:20px;" @click="send_msg">提交评论</el-button>

            </div>
        </div>
        <div class="content-right">
            <follow></follow>
        </div>
       <!-- <back></back>-->

    </div>

</template>


<script>
    import follow from '../../components/follow.vue'

    export default {
        name: 'messages',

        components:{
            "follow":follow,

        },
        props:['messages'],
        data() {
            return {
                currentPage: 1, //初始页
                pagesize: 5, //    每页的数据
                total: 0, // 总共的条数
                share:false,
                circular:false,
                count:0, //+1的字显示状态用
                numberLikes:666, //点赞的数量
                textarea: '',
                inputName:'',
                inputImg:'',
                inputNum:'',
                systemTime:'',
                msg_arr:[],
            }
        },
        mounted(){
            let _this=this;
            //后台的数据赋值到msg_arr数组里

                this.messages.text.forEach(function (item) {
                    _this.msg_arr.push(item);
                });
          this.addDate()
        },
  /*      created(){
            this.$http('/messages', 'post', {}).then((res) => {
                this.messages = res.data.data;
            });

        },*/
        methods: {
            handleSizeChange (size) {
                this.pagesize = size;
            },
            handleCurrentChange (currentPage) {
                this.currentPage = currentPage;
            },
            sign(){
                alert('研发中。。。')
            },
            open() {
                let self=this;
                this.count++;
                if(this.count<=1){
                    this.numberLikes++;
                    this.$alert('谢谢你！', {
                        confirmButtonText: '确定',
                    });
                    setTimeout(function () {
                        self.count++;
                    }, 1000)

                }else {
                    alert('只能点赞一次')
                }
            },
            send_msg () {
                if(this.textarea != ''&& this.inputName!='' && this.inputImg != ''){
                    if (this.textarea) {
                        this.msg_arr.unshift({name:this.inputName,active:this.inputImg,time:this.systemTime,number:this.msg_arr.length+1,content:this.textarea});
                        this.textarea = '';
                        this.inputName='';
                        this.inputImg='';
                    }
                }else {
                    alert('输入不能为空')
                }
            },
            addDate () {
                let nowDate = new Date()
                let date = {
                    year: nowDate.getFullYear(),
                    month: nowDate.getMonth() + 1,
                    date: nowDate.getDate()
                };
                this.systemTime =date.year + '-'+date.month + '-' + date.date
            }
        },
        computed:{

            textCopy(){ /*将后台数据拆分*/
                let spnCopy=[];
                    spnCopy=this.msg_arr.slice((this.currentPage-1)*this.pagesize,this.currentPage*this.pagesize);
                return spnCopy ;
            }
        },
        created(){
            window.scrollTo(0,0);
        }
    }
</script>

<style lang="scss" scoped>
    #messages{
        width:60%;
        margin: 0 auto;
        box-shadow:0 0 15px 5px #888;
        padding:20px;
        background:#fafafa;
        display: flex;
        .content{
            width:80%;
            flex-wrap: wrap;
            padding: 0 20px;

            .messages-head{
                width:100%;
                border: 1px solid lightgrey;
                padding:10px 0;
                border-left:none;
                border-right:none;
                .head-img{
                    width:100px;
                    height:100px;
                    transition:all 1s;
                    border-radius:50%;
                    border: 1px solid black;
                    display:inline-block;
                    margin-right:20px;
                    &.head-img:hover{
                        transform:rotate(360deg);
                    }
                    img{
                        width:100px;
                        height:100px;
                        border-radius:50%;
                    }
                }
                .title{
                    display:inline-block;
                    font-size:30px;
                    vertical-align:top;
                    font-weight:bold;
                    .text{
                        margin-top:20px;
                        font-size:16px;
                        color:gray;
                        i{
                            margin-right:5px;
                        }
                        span{
                            margin-right: 30px;
                        }
                        .one{
                            margin-left:-30px;
                            color:orangered;

                            &.black-enter-active{
                                animation: ai 2s
                            }
                            &.black-leave-active{
                                animation: ai 2s reverse
                            }
                            @keyframes ai{
                                from{opacity:0 }
                                to{opacity:1}
                            }
                        }
                    }
                }
            }
            .head-content{
                padding:36px;
                .text{
                    font-size:20px;
                    padding: 10px;
                    border-bottom:1px solid lightgrey;
                    background:skyblue;
                    margin-bottom:20px;

                }
                .text:nth-child(2){
                    background: #acffe5;
                }
                .text:last-child{
                    background: #ff7b98;
                }
            }
            .fabulous-share{
                display: flex;
                justify-content: center;
                align-items: center;
                .fabulous{
                    width: 150px;
                    height: 50px;
                    background: pink;
                    display: inline-block;
                    margin-right: 10px;
                    text-align: center;
                    font-size: 20px;
                    line-height: 50px;
                    color:white;
                    &.fabulous:hover{
                        background: gray;
                    }

                    i{
                        font-size: 20px;
                    }
                    span{
                        margin-right: 30px;
                    }
                }
                .share{
                    width: 150px;
                    height: 50px;
                    background: pink;
                    display: inline-block;
                    text-align: center;
                    font-size: 20px;
                    line-height: 50px;
                    color:white;
                    &.share:hover{
                        background: gray;
                    }
                    i{
                        font-size: 20px;
                        margin-left: 30px;
                    }
                    span{
                        margin-left: 2px;
                    }
                }
                .big-circular{
                    width: 80px;
                    height: 80px;
                    display: inline-block;
                    border-radius:50%;
                    background:#fafafa ;
                    margin-left: -195px;
                    padding:16px;

                    .small-circular{
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                        background:gold;
                        text-align:center;
                        line-height:50px;
                        &.small-circular:hover{
                            background: gray;
                        }
                        i{
                            font-size:30px;
                            color:white;
                        }
                    }



                }



            }
            .messages-list{
                &.messages-list:hover{
                    .bor{
                        border-bottom: 1px solid #3ea6ff;
                    }
                }
                .bor{
                    border-bottom:1px solid lightgrey;
                    transition:all 1s;
                    h2{
                        display:inline-block;
                    }
                    i{
                        color: #ffa856;
                        margin-right:5px;
                    }
                }
                .list{
                    width:100%;
                    background: rgba(7,17,27,0.1);
                    margin: 20px 0;
                    .list-content{
                        width: 100%;
                        padding:16px;
                        .list-img{
                            display: inline-block;
                            border-radius:50%;
                            width: 50px;
                            height: 100%;
                            margin-right:10px;
                            transition:all 1s;
                            &.list-img:hover{
                                transform:rotate(360deg);
                            }
                            img{
                                border-radius:50%;
                                width: 50px;
                                height: 50px;
                            }
                        }
                        .list-name{
                            display:inline-block;
                            vertical-align:top;
                            font-size:16px;
                            width: 80%;
                            span:first-child{
                                margin-right:10px;
                                font-weight:bold;
                            }
                            .list-text{
                                margin-top: 5px;
                            }
                        }
                        .list-number{
                            float:right;
                            font-size:16px;
                            color: #3ea6ff;
                        }
                    }
                }
            }
            .publish-messages{
                margin-top:20px;
                &.publish-messages:hover{
                    .bor{
                        border-bottom: 1px solid #3ea6ff;
                    }
                }
                .bor{
                    border-bottom:1px solid lightgrey;
                    transition:all 1s;
                    h2{
                        display:inline-block;
                    }
                    i{
                        color: #ffa856;
                        margin-right:5px;
                        font-size:20px;
                    }

                }
            }
        }
        .content-right{
            width:20%;

        }
    }

</style>