import Vue from 'vue'
import VueRouter from 'vue-router'
import page from '../views/page/page.vue'
import blogs from '../views/blogs/blogs.vue'
import messages from '../views/messages/messages.vue'
import diary from '../views/diary/diary.vue'
import interlinkage from '../views/interlinkage/interlinkage.vue'
import aboutme from  '../views/aboutme/aboutme.vue'
import blogcontent from '../views/blogcontent/blogcontent.vue'



Vue.use(VueRouter)

const routes = [
    {
       path:'/',
       redirect:'/page'
    },

    {
       path: '/page',
       name: 'page',
       component:page,

     },
    {
        path:'/blogs',
        name:'blogs',
        component:blogs,

    },
    {
        path:'/messages',
        name:'messages',
        component:messages
    },
    {
        path:'/diary',
        name:'diary',
        component:diary
    },
    {
        path:'/interlinkage',
        name:'interlinkage',
        component:interlinkage
    },
    {
        path:'/aboutme',
        name:'aboutme',
        component:aboutme
    },
    {
        path:'/blogs/:id',
        name:'blogcontent',
        component:blogcontent
    }
];

const router = new VueRouter({
  /*mode: 'history',*/
  base: process.env.BASE_URL,
  routes
});

export default router
