<template>
    <div id="blogs">

        <div class="blogs-left animate__animated animate__zoomIn" >
            <div class="content" v-for="(item,index) in smallBlogs" :key="index" ><!--文章-->
                <img src="../../assets/01.png" width="200" height="200">
                <div class="text">
                    <h1  @click="jump(item.id)">{{item.title}}</h1>
                    <div>{{item.brief}}</div>
                    <div class="tape">{{item.type}}</div>
                </div>
            </div>
            <!--分页器组件-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5, 10, 20, 40]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="blogs.length"
                    v-show="flag">
            </el-pagination>

        </div>

        <div class="blogs-right">
            <follow :blogs="blogs"></follow><!--右边部分的组件-->
        </div>

    </div>

