<template>
    <div class="follow">
        <div class="business-card  animate__animated animate__zoomIn">
            <div class="title">
                关注我
            </div>
            <div class="people">
                世界之大<br>总能有几个懂你的人
            </div>
            <!--hover对话框-->
            <el-popover
                    placement="top-start"
                    title=""
                    width="200"
                    trigger="hover"
                    content="点击我加我微信">
                <div class="apps two"  slot="reference"><i class="iconfont icon-weixin1" @click="cir = true"></i></div>
            </el-popover>
            <!--hover对话框-->
            <el-popover
                    placement="top-start"
                    title=""
                    width="200"
                    trigger="hover"
                    content="点击我与我QQ交流"
                    >
                <div class="apps" slot="reference" ><i class="iconfont icon-qq" @click="cir = true" ></i></div>
            </el-popover>
            <!--点击的对话框-->
            <el-dialog
                    :visible.sync="cir"
                    width="20%"
                    center
                    append-to-body
                    >
                <div style="width: 100px;height: 100px;border:1px solid black;text-align: center;margin: 0 auto">
                    暂无二维码
                </div>
                <span slot="footer" class="dialog-footer">
                          <el-button @click="cir = false">取 消</el-button>
                          <el-button type="primary" @click="cir = false">确 定</el-button>
                    </span>
            </el-dialog>
            <div class="card-bottom">加入前端成长之路</div>
        </div>
        <div class="cloud  animate__animated animate__zoomIn"><!--标签云-->
            <div class="title">
                标签云
            </div>
            <svg :width='width' :height='height' @mousemove='listener($event)'  >
                <a :href="tag.href" v-for='(tag,index) in tags' :key="index">
                    <text :x='tag.x' :y='tag.y' :font-size='20 * (600/(600-tag.z))' :fill-opacity='((400+tag.z)/600)'>{{tag.text}}</text>
                </a>
            </svg>
        </div>
   <!--     <div class="hot">
            <div class="title">
                热门文章
            </div>
            <div class="num"><Tag checkable color="orangered">1</Tag></div>
            <div class="num"> <Tag checkable color="error">2</Tag></div>
            <div class="num"> <Tag checkable color="warning">3</Tag></div>
            <div class="num"><Tag checkable color="skyblue">4</Tag></div>
            <div class="num"><Tag checkable color="success">5</Tag></div>

        </div>-->

    </div>
</template>
<script>
    export default {
        name: 'follow',
        props:['blogs'],
        data(){
            return {
                width:180,//svg宽度
                height:250,//svg高度
                tagsNum:3,//标签数量
                RADIUS:70,//球的半径
                speedX:Math.PI/360,//球一帧绕x轴旋转的角度
                speedY:Math.PI/360,//球-帧绕y轴旋转的角度
                tags: ['css','js','vue'],
                blogsCopy:[],
                cir: false
            }
        },
        computed:{
            CX(){//球心x坐标
                return this.width/2.7;
            },
            CY(){//球心y坐标
                return this.height/1.9;
            },
      /*      blogsHot(){
                let arr=this.blogsCopy;
                this.arr.sort(function (a,b) {
                    return b.hot-a.hot
                });
                return arr
            }*/
        },
        created(){//初始化标签位置

            let tags=[];
            for(let i = 0; i < this.tags.length; i++){
                let tag = {};
                let arr=['css','js','vue'];
                let k = -1 + (2 * (i + 1) - 1) / this.tagsNum;
                let a = Math.acos(k);
                let b = a * Math.sqrt(this.tagsNum * Math.PI);//计算标签相对于球心的角度
                tag.text = arr[i];
                tag.x = this.CX +  this.RADIUS * Math.sin(a) * Math.cos(b);//根据标签角度求出标签的x,y,z坐标
                tag.y = this.CY +  this.RADIUS * Math.sin(a) * Math.sin(b);
                tag.z = this.RADIUS * Math.cos(a);
                tag.href = 'http://192.168.8.28:8080/blogs';//给标签添加链接
                tags.push(tag);
            }
            this.tags = tags;//让vue替我们完成视图更新
        },
        mounted(){//使球开始旋转
            this.blogsCopy=this.blogs;

            setInterval(()=>{
                this.rotateX(this.speedX);
                this.rotateY(this.speedY);
            }, 17)
        },
        methods:{
            rotateX(speedX){
                var cos = Math.cos(speedX);
                var sin = Math.sin(speedX);
                for(let tag of this.tags){
                    var y1 = (tag.y- this.CY) * cos - tag.z * sin  + this.CY;
                    var z1 = tag.z * cos + (tag.y- this.CY) * sin;
                    tag.y = y1;
                    tag.z = z1;
                }
            },
            rotateY(speedY){
                var cos = Math.cos(speedY);
                var sin = Math.sin(speedY);
                for(let tag of this.tags){
                    var x1 = (tag.x - this.CX) * cos - tag.z * sin + this.CX;
                    var z1 = tag.z * cos + (tag.x - this.CX) * sin;
                    tag.x = x1;
                    tag.z = z1;
                }
            },
            listener(event){//响应鼠标移动
                var x = event.clientX - this.CX;
                var y = event.clientY - this.CY;
                this.speedX = x*0.0001>0 ? Math.min(this.RADIUS*0.00002, x*0.0001) : Math.max(-this.RADIUS*0.00002, x*0.0001);
                this.speedY = y*0.0001>0 ? Math.min(this.RADIUS*0.00002, y*0.0001) : Math.max(-this.RADIUS*0.00002, y*0.0001);
            },
            big(){
                alert('研发中')
            }
        }
    }
</script>

