
/*发送请求*/

import axios from 'axios'
function http(url,methods,data) {
    return new Promise((resolve,reject)=>{
        axios({
            method:methods,
            url:url,
            data:data,
        }).then((res)=>{
            resolve(res);
        }).catch((err)=>{
            reject(err)
        })
    })
}
export {http}
